import { Center, HStack, Td, Text, Tr, VStack } from '@chakra-ui/react';
import React, { useEffect, useState } from 'react';
import { formatMs, StopwatchUtils, Stopwatch, StopwatchState } from 'utils';
import { LapData } from './lapData';
import { RowControls } from './rowControls';

interface StopwatchRowProps {
  sw: Stopwatch;
  index: number;
  stop: () => void;
  lap: () => void;
  remove: () => void;
  setName: (name: string) => void;
  maxLaps: number;
}

export const StopwatchRow = ({
  sw,
  index,
  stop,
  lap,
  remove,
  setName,
  maxLaps,
}: StopwatchRowProps) => (
  <Tr>
    <Td m={0} p={0}>
      <RowControls sw={sw} stop={stop} lap={lap} index={index} remove={remove} setName={setName} />
    </Td>

    {sw.state !== StopwatchState.NOT_STARTED && (
      <>
        <CurrentTime sw={sw} />
        {Array.from({ length: maxLaps }).map((_, i) =>
          maxLaps - i - 1 < sw.laps.length ? (
            <LapData
              key={maxLaps - i}
              laps={sw.laps}
              startTime={sw.startTime}
              index={maxLaps - i - 1}
            />
          ) : (
            <Td />
          ),
        )}
      </>
    )}
  </Tr>
);

const CurrentTime = ({ sw }: { sw: Stopwatch }) => {
  const [now, setNow] = useState<number>(Date.now());

  useEffect(() => {
    const intervalId = setInterval(() => {
      setNow(Date.now());
    }, 20);

    return () => clearInterval(intervalId);
  }, []);

  return (
    <Td m={0} p={0}>
      <VStack spacing={0}>
        <Text fontSize={'xl'} as={'b'}>
          {formatMs(StopwatchUtils.elapsed(sw, now))}
        </Text>
        <Text fontSize={'sm'}>{formatMs(StopwatchUtils.currentLap(sw, now))}</Text>
      </VStack>
    </Td>
  );
};
